import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import {LocalStorageService, SessionStorageService} from 'ngx-webstorage';
import { Observable, Subject } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { tap, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class DataServiceService {
  public userData:any;
  public allUserData:any;
  userDataView(){
    return this.allUserData;
  }
  getuNameUsers(){
    const newUsers = [];
      for(var key in localStorage){
        if(key!='length'){newUsers.push(key.substr(15));}
     else {break;}
     }
     return newUsers;
  }
  RegUser(Reg:any) {
    this.localSt.store(Reg.regUserId, Reg);
  }
  LoginUser(Login:any) {
    this.allUserData=[];
    for (let i = 0; i < localStorage.length; i++){
      let key = localStorage.key(i);
      let value = localStorage.getItem(key);
      this.allUserData.push(JSON.parse(value));
    }
    this.userData=this.localSt.retrieve(Login.loginUserId);
    
    return this.userData;
  }
  constructor(private _http: HttpClient,private localSt:LocalStorageService) { }
}
