import { Directive, forwardRef, Attribute } from '@angular/core';
import { Validator, AbstractControl, NG_VALIDATORS } from '@angular/forms';
@Directive({
    selector: '[appPasswordValidate][formControlName],[appPasswordValidate][formControl],[appPasswordValidate][ngModel]',
    providers: [
        { provide: NG_VALIDATORS, useExisting: forwardRef(() => PasswordValidateDirective), multi: true }
    ]
})
export class PasswordValidateDirective implements Validator {
    constructor(@Attribute('appPasswordValidate') public appPasswordValidate: string,
    @Attribute('reverse') public reverse: string) {
    }

    private get isReverse() {
        if (!this.reverse) return false;
        return this.reverse === 'true' ? true: false;
    }

    validate(c: AbstractControl): { [key: string]: any } {
        // self value
        let v = c.value;

        // control vlaue
        let e = c.root.get(this.appPasswordValidate);

        // value not equal
        if (e && v !== e.value && !this.isReverse) {
            return {
                appPasswordValidate: false
            }
        }

        // value equal and reverse
        if (e && v === e.value && this.isReverse) {
            delete e.errors['appPasswordValidate'];
            if (!Object.keys(e.errors).length) e.setErrors(null);
        }

        // value not equal and reverse
        if (e && v !== e.value && this.isReverse) {
            e.setErrors({ appPasswordValidate: false });
        }

        return null;
    }
}