import { Component, OnInit } from '@angular/core';
import { DataServiceService } from '../data-service.service';
import * as $ from 'jquery';
import { NgForm  } from '@angular/forms';
import { Router } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  public userId:string;
  uNameArray: any=[];
  uNameLoginexist:any=true;
  uNameRegexist:any=false;
  loginPassValid:any=false;
  loginUserData:any;

  myRecaptcha: boolean
 
    onScriptLoad() {
        console.log('Google reCAPTCHA loaded and is ready for use!')
    }
 
    onScriptError() {
        console.log('Something went long when loading the Google reCAPTCHA')
    }

  constructor(public DataService:DataServiceService,private router : Router) { }
    public logUser(logForm: NgForm): any
  {
    let retData=this.DataService.LoginUser(logForm.value);
    if(retData.regPassword==logForm.value.loginPassword)
    {
      this.loginPassValid=true;
      this.loginUserData=retData;
      this.router.navigateByUrl('/view');
    }
    
  }
  public registerUser(regForm: NgForm): any {
    //let RegUserData=Object.assign(regForm.value,{"Qualification":this.Qualification.join(',')});
    console.log(regForm.value);
    this.DataService.RegUser(regForm.value);
  }
  inputChanged(val1: any,val2:string) {
    if (val2=='Reg')
    {
      this.uNameRegexist=this.uNameArray.indexOf(val1)>-1;
    }
    else{
      this.uNameLoginexist=this.uNameArray.indexOf(val1)>-1;  
    }
  }
  ngOnInit() {
    this.uNameArray=this.DataService.getuNameUsers();
    console.log(this.uNameArray);
    
    $(".GenderRdl").click(function(){
      if($("input:radio[name='regGender']").is(":checked")) {
        $(".GenderRdl").attr('style','border-left:5px solid green;border-top:5px solid lightslategray;border-bottom:5px solid lightslategray;width:30vw; height: 10vh;border-radius: 50px;padding: 3%;border-right:5px solid lightslategray;');
      }
  });
  }

}
